.skill-area{
    background: #191919;
    background-size: cover;
    height: auto;
    font-family: Sfpro !important;
    padding: 80px 0 80px 0;
}.skill-head-txt{
    color: #fff;
    font-size: 26px;
    font-weight: 500;
    
}


.bars-area{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    justify-content: space-between;
    padding: 10px 80px 0 80px;
    transition: all 0.9s ease;
    
}
/* for bars */



.skill-bars{
  background: transparent;
  border-radius: 10px;
}
.skill-bars .bar{
  margin: 20px 0;
}
.skill-bars .bar:first-child{
  margin-top: 0px;
}
.skill-bars .bar .info{
  margin-bottom: 5px;
}
.skill-bars .bar .info span{
  font-weight: 500;
  font-size: 18px;
  opacity: 0;
  animation: showText 0.5s 1s linear forwards;
}
@keyframes showText {
  100%{
    opacity: 1;
  }
}
.info{
    color: #fff;
}
.skill-bars .bar .progress-line{
  height: 10px;
  width: 100%;
  background: #f0f0f0;
  position: relative;
  transform: scaleX(0);
  transform-origin: left;
  border-radius: 10px;
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.05),
              0 1px rgba(255,255,255,0.8);
  animation: animate 1s cubic-bezier(1,0,0.5,1) forwards;
}
@keyframes animate {
  100%{
    transform: scaleX(1);
  }
}
.bar .progress-line span{
  height: 100%;
  position: absolute;
  border-radius: 10px;
  transform: scaleX(0);
  transform-origin: left;
  background: #1ab66d;
  animation: animate 1s 1s cubic-bezier(1,0,0.5,1) forwards;
}
.bar .progress-line.html span{
  width: 90%;
}
.bar .progress-line.css span{
  width: 85%;
}
.bar .progress-line.javascript span{
  width: 77%;
}
.bar .progress-line.python span{
  width: 50%;
}
.bar .progress-line.mongodb span{
  width: 63%;
}
.bar .progress-line.node span{
    width: 75%;
}
.bar .progress-line.c span{
    width: 55%;
}
.bar .progress-line.react span{
    width: 80%;
}
.bar .progress-line.firebase span{
    width: 85%;
}
.progress-line span::before{
  position: absolute;
  content: "";
  top: -10px;
  right: 0;
  height: 0;
  width: 0;
  border: 7px solid transparent;
  border-bottom-width: 0px;
  border-right-width: 0px;
  border-top-color: #000;
  opacity: 0;
  animation: showText2 0.5s 1.5s linear forwards;
}
.progress-line span::after{
  position: absolute;
  top: -28px;
  right: 0;
  font-weight: 500;
  background: #000;
  color: #fff;
  padding: 1px 8px;
  font-size: 12px;
  border-radius: 3px;
  opacity: 0;
  animation: showText2 0.5s 1.5s linear forwards;
}
@keyframes showText2 {
  100%{
    opacity: 1;
  }
}
.progress-line.html span::after{
  content: "90%";
}
.progress-line.css span::after{
  content: "85%";
}
.progress-line.javascript span::after{
  content: "77%";
}
.progress-line.python span::after{
  content: "50%";
}
.progress-line.mongodb span::after{
  content: "63%";
}
.progress-line.c span::after{
    content: "55%";
  }
  .progress-line.node span::after{
    content: "75%";
  }
  .progress-line.react span::after{
    content: "80%";
  }
  .progress-line.firebase span::after{
    content: "85%";
  }
@media only screen and (max-width:660px) {
  .progress-line span::after{
    display: none;
  }
}
@media only screen and (max-width:1170px) {
  .bars-area{
    padding: 10px 20px 0 20px;
    transition: all 0.9s ease;
    
}
}
  /* for sm */
  
@media only screen and (max-width:800px) {
  .bars-area{
    padding: 10px 10px 0 10px;
    
}
}
@media only screen and (max-width:500px) {
  .bars-area{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    justify-content: space-between;
    padding: 5px 5px 0 5px;
}.skill-bars .bar .info span{
  font-weight: 500;
  font-size: 15px;
  opacity: 0;
  animation: showText 0.5s 1s linear forwards;
}
.skill-head-txt{
  font-size: 23px;
}

}
@media only screen and (max-width:370px) {
  .skill-head-txt{
    font-size: 21px;
  }.bars-area{
    padding: 5px 1px 0 1px;
}
}
@media only screen and (max-width:280px) {
  .skill-bars .bar .info span{
    font-size: 12px;
  }.skill-head-txt{
    font-size: 18px;
  }
}