@font-face {
  font-family: SfPro;
  src: url(/src/fonts/FontsFree-Net-SFProDisplay-Regular.ttf);
}
@font-face {
  font-family: JumperPro;
  src: url(/src/fonts/JumperPERSONALUSEONLY-BoldItalic.ttf);
}
::-moz-selection { /* Code for Firefox */
  color: #fff;
  background: #1ab66d;
}

::selection {
  color: #fff;
  background: #1ab66d;
}
*{
  scroll-behavior: smooth;
}
/* width */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #1ab66d; 
  border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #119758; 
}