.allservices-area{
    width: 100%;
    height: auto;
    background: #191919;
    background-size: cover;
    padding: 30px 0 130px 0;
    font-family: Sfpro;
}
.allservices-area h4{
    color: #f5f4f4;
    margin: 40px auto auto auto;
    font-size: 26px;
}
.allservices-area h3{
    color: #fff;
    margin: 10px auto auto auto;
    font-size: 45px;
}
.services-boxs-area{
    padding: 20px 140px 10px 140px;
    width: 100%;
    height: auto;
    margin: 50px 0 0 0;
}
.services-card{
    width: 310px;
    height: auto;
    background: #232428;
    border-radius: 1rem;
    margin: 10px auto 50px auto ;
    padding: 5px 10px 30px 20px;
    transition: all 0.6s ease;
}

.service-card-content{
    display: flex;
    flex-direction: column;
}
.service-card-content i{
    
    font-size: 38px;
    margin: 45px 0px 0 20px;
}
.service-card-content h2{
    color: #fff;
    font-size: 34px;
    margin: 20px 0 0 0;
}
.service-card-content p{
    color: #8b8c8e;
    font-size: 18px;
    margin: 3px 0 0 0;
}
.contact-me-allSer{
    margin: 10px auto auto auto;
    width: 200px ;
    height: 60px;
    background: transparent;
    border: 2px solid #1ab66d;
    border-radius: 1rem;
}
.contact-me-allSer button{
    width: 100% ;
    height: 100%;
    background: transparent;
    border: none !important;
    color: #1ab66d;
    font-size: 18px;
    border-radius: 0.7rem;
    transition: all 0.3s ease;
}
.contact-me-allSer button:hover{
    background: #1ab66d;
    border: 2px solid #1ab66d;
    color: #111;
}
.card-span{
    width: 100%;
    height: 100%;
    color: #1ab66d;
}
.services-card:hover{
    background: #1b1b1f;
    transform: scale(1.02);
    transition: all 0.4s ease;
}
.card-span:hover{
    color: #ffffff;
}
